body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-gradient(linear, left top, right bottom, from(#60A0C0), to(#048));
  background: -webkit-linear-gradient(top left, #60A0C0, #048);
  background: -o-linear-gradient(top left, #60A0C0, #048);
  background: linear-gradient(to bottom right, #60A0C0, #048);
  width: 100vw;
  height: 100vh;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}



